
import React, { Component } from 'react';
import { Dropdown, Accordion, Nav, Tab, ToggleButtonGroup, ToggleButton, OverlayTrigger, Tooltip } from 'react-bootstrap';
import AndOrCondition from './AndOr'

class Collections extends Component {
    render() {


        const { state, onCollectionSearchFilter, onCollectionSelectAll, onCollectionApply, conditionApply, filterApply, filterKey, indexFilter, isLast, onSelectActive } = this.props
        return <Accordion activeKey={state.activeKey} onSelect={onSelectActive(filterKey) ? onSelectActive(filterKey) : null}>
            <Accordion.Item eventKey={'collection'} className="pb-2">
                <div className="d-flex">
                    <Accordion.Header className="me-1">
                        <div className="d-flex flex-grow-1 align-items-center pe-4">
                            <span className="svg-icon text-gray-800 d-flex">
                                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="24" height="24"><path fill="none" d="M0 0h24v24H0z" /><path d="M12.414 5H21a1 1 0 0 1 1 1v14a1 1 0 0 1-1 1H3a1 1 0 0 1-1-1V4a1 1 0 0 1 1-1h7.414l2 2zM8 19h12v-8H8v8zm-2 0v-9a1 1 0 0 1 1-1h13V7h-8.414l-2-2H4v14h2z" fill="currentColor" /></svg>
                            </span>
                            <div className="fs-9 fw-medium">Collection</div>
                        </div>
                        <button type="button" className="close_filter btn btn-icon btn-sm position-absolute end-0" onClick={filterApply(filterKey)}></button>
                    </Accordion.Header>
                    <AndOrCondition condition={state.condition} conditionApply={conditionApply} />
                </div>
                <Accordion.Body className="border rounded-1 mt-1">
                    {state.collectionList && Object.keys(state.collectionList).length > 0 ?
                        <>
                            <div className="navi-item d-flex align-items-center py-2 cursor-pointer">
                                <label className="form-check flex-grow-1" htmlFor="searchCollection">
                                    <div className="navi-text">
                                        <p className="fs-9 fw-medium mb-0">Search Collection</p>
                                    </div>
                                </label>
                                <input className="form-check-input" type="checkbox" name="" value="phone" onClick={onCollectionSelectAll} checked={Object.keys(state.selectedCollection).length >= state.collectionList.length ? true : false} />
                            </div>
                            <div className="form-group mb-2">
                                <input type="text" className="form-control form-control-sm" id="searchCollection" placeholder="Search your collection..." onChange={onCollectionSearchFilter} />
                            </div>
                            <div className="">
                                {
                                    state.collectionList.map((item, indexC) => <div key={indexC} className="dropdown-item px-0"><label className="form-check flex-grow-1 overflow-hidden align-items-center" htmlFor={`collection_${item.id}`}>
                                        <span className="symbol symbol-22 bg-light">
                                            {item.thumbnails?
                                            <img className="object-center-cover" src={item.thumbnails} height={20} width={20} alt={item.name} />
                                            :<span className="svg-icon me-0 text-gray-700">
                                                <svg className="w-14px h-14px" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"><path d="M3 21C2.44772 21 2 20.5523 2 20V4C2 3.44772 2.44772 3 3 3H10.4142L12.4142 5H20C20.5523 5 21 5.44772 21 6V9H19V7H11.5858L9.58579 5H4V16.998L5.5 11H22.5L20.1894 20.2425C20.0781 20.6877 19.6781 21 19.2192 21H3ZM19.9384 13H7.06155L5.56155 19H18.4384L19.9384 13Z" fill="currentColor"></path></svg>
                                            </span>}
                                        </span>
                                        <span className="form-check-label text-gray-800 text-ellipsis">{item.name}</span>
                                    </label>
                                        <input className="form-check-input flex-shrink-0" type="checkbox" name={`collection_${item.id}`} id={`collection_${item.id}`} checked={Object.keys(state.selectedCollection).includes(`${item.id}`) ? true : false} onClick={onCollectionApply(item.id)} />
                                    </div>)
                                }
                            </div>
                        </>
                        :
                        <div className="py-4">
                            <h6 className="text-center fs-9 mb-0 text-gray-700">No Collections Available</h6>
                        </div>
                    }
                </Accordion.Body>
            </Accordion.Item>
        </Accordion>
    }
}

export default Collections
